<template>
  <div>
    <div class="row">
      <div class="col-3">
        <h4 class="ms-4">Profile</h4>
      </div>
      <div class="col-6">
        <v-snackbar v-model="snackbar" :timeout="timeout" type="success" border="left" elevation="3">
          <template v-slot:action="{ attrs }">
            <v-btn color="blue"  v-bind="attrs" text @click="snackbar = false">Close</v-btn>
          </template>
          {{text}}
        </v-snackbar>
      </div>
    </div>
    <v-tabs v-model="tab">
            <v-tab>General</v-tab>
            <v-tab>Business Information</v-tab>
            <v-tab>Change Password</v-tab>
            <v-tab>Account Information</v-tab>
          </v-tabs>
    <v-tabs-items v-model="tab">
            <v-tab-item>
              <v-card color="basil" flat >
                <v-form ref="form" v-model="valid">
                  <v-row>
                    <v-col cols="12" md="12">
                      <div v-if="files != '' ">
                        <v-avatar size="100" v-for="(file, f) in files" :key="f">
                          <img :ref="'file'"  style="display:block">
                        </v-avatar>
                      </div>
                      <div v-else-if="bprofiles.user_avatar != null ">
                        <v-avatar size="100">
                          <img :src=(url+bprofiles.user_avatar)>
                        </v-avatar>
                      </div>
                      <div v-else>
                        <v-avatar size="100" color="error">
                          <span class="white--text fw-bold text-h4">{{bprofiles.name.charAt(0)}}{{firstCharOfLastWord}}</span>
                        </v-avatar>
                      </div>
                    </v-col>
                    <v-col cols="12" md="6">
                      <v-file-input label="Add Image" v-model="files" multiple small-chips accept="image/*" @change="addFiles" v-on:click="isHidden = true"></v-file-input>
                    </v-col>
                    <v-col cols="12" md="6">
                      <v-text-field label="Enter your business name" v-model="bprofiles.name" :rules="nameRules" :counter="100" required ></v-text-field>
                    </v-col>
                    <v-col cols="12" md="6">
                      <v-text-field type="email" label="Enter e-mail address" v-model="bprofiles.email" :rules="emailRules" required ></v-text-field>
                    </v-col>
                    <v-col cols="12" md="6">
                      <v-text-field label="Enter your username" v-model="bprofiles.username" :rules="usernameRules" :counter="80" required ></v-text-field>
                    </v-col>
                    <v-col cols="12" md="6">
                      <v-text-field label="Enter mobile" v-model="bprofiles.mobile_phone" :rules="mobileRules" :counter="10" required ></v-text-field>
                    </v-col>
                  </v-row>
                  <div v-if="userdata.id != null">
                    <v-btn class="my-2 ms-4 cred" :disabled="!valid" @click="updateProfileByreseller">UPDATE</v-btn>
                  </div>
                  <div v-else>
                    <v-btn class="my-2 ms-4 cred" :disabled="!valid" @click="updateProfile1">UPDATE</v-btn>
                  </div>

                </v-form>
              </v-card>
            </v-tab-item>
            <v-tab-item >
              <v-card color="basil" flat >
                <v-form ref="form" v-model="valid">
                  <v-row>
                    <v-col cols="12" md="6">
                      <v-autocomplete label="Select Category" v-model="catId" @change="getsubCategory()" @selected="getsubCategory()" :rules="catRules" :items="categories" item-text="cat_name" item-value="id" autocomplete="on" ></v-autocomplete>
                    </v-col>
                    <v-col cols="12" md="6">
                      <v-autocomplete  label="Select Sub Category" v-model="subids" multiple :rules="subcatRules" :items="subcategories" item-text="subcat_name" item-value="id"></v-autocomplete>
                    </v-col>
                    <v-col cols="12" md="6">
                      <v-autocomplete label="Select State" v-model="stateId" @change="getCities()"
                                      @selected="getCities()" :rules="stateRules" :items="allstates" item-text="state" item-value="id"></v-autocomplete>
                    </v-col>
                    <v-col cols="12" md="6">
                      <v-autocomplete label="Select City" v-model="bprofiles.city_id" :rules="cityRules" :items="allcities" item-text="city" item-value="id"></v-autocomplete>
                    </v-col>
                    <v-col cols="12" md="6">
                      <v-text-field  label="Enter GST Number" v-model="bprofiles.gst" required ></v-text-field>
                    </v-col>
                    <v-col cols="12" md="6" >
                      <v-text-field type="number" v-model="bprofiles.mobile_phone" :rules="mobileRules" :counter="10" label="Enter Whatsapp Number" required ></v-text-field>
                    </v-col>
                    <v-col cols="12" md="12" >
                      <v-textarea rows="2" row-height="20" v-model="bprofiles.address" :rules="addressRules" label="Enter Address"></v-textarea>
                    </v-col>
                    <v-col cols="12" md="12" >
                      <label class="fw-bold">About</label>
                      <ckeditor v-model="bprofiles.about"></ckeditor>
                    </v-col>
                    <v-col cols="12" md="12" >
                      <label class="fw-bold">Services</label>
                       <ckeditor v-model="bprofiles.service_text"></ckeditor>
                    </v-col>
                    <v-col cols="12" md="12" v-for="(tab, index) in tabsarray" :key="'tab-' + index">
                      <label class="fw-bold">{{ tab.title }}</label>
                      <ckeditor :editor="tab" :name="'tab' + index" v-model="tab.tab_content"></ckeditor>
                    </v-col>
                  </v-row>
                  <div v-if="userdata.id != null">
                    <v-btn class="my-2 ms-4 cred" :disabled="!valid" @click="updateProfile2byreseller">UPDATE</v-btn>
                  </div>
                  <div v-else>
                    <v-btn class="my-2 ms-4 cred" :disabled="!valid" @click="updateProfile2">UPDATE</v-btn>
                  </div>
                </v-form>
              </v-card>
            </v-tab-item>
            <v-tab-item >
              <v-card color="basil" flat >
                <v-form ref="form" v-model="valid">
                  <v-row>
                    <v-col cols="12" md="12">
                      <v-text-field v-model="password" type="password" :rules="passwordRules" label="Change Password" hint="Leave empty if you don't want to change the password." required ></v-text-field>
                    </v-col>
                  </v-row>
                  <div v-if="userdata.id != null">
                    <v-btn color="my-2 cred" :disabled="!valid" @click="updatePasswordbyreseller">UPDATE</v-btn>
                  </div>
                  <div v-else>
                    <v-btn color="my-2 cred" :disabled="!valid" @click="updatePassword">UPDATE</v-btn>
                  </div>
                </v-form>
              </v-card>
            </v-tab-item>
            <v-tab-item >
              <v-card color="basil" flat >
                <v-form>
                  <v-row>
                    <v-col cols="12" md="12" v-if="reseller.length > 0">
                      <h5 class="ms-4">Reseller
                        <span class="h6 text-muted">({{reseller[0].city}})</span>
                      </h5>
                    </v-col>
                    <v-col cols="12" md="12" v-else>
                      <h5 class="ms-4">Business
                        <span class="h6 text-muted">({{bprofiles.city}})</span>
                      </h5>
                    </v-col>
                      <v-col cols="12" md="12" v-if="reseller.length > 0">
                        <v-text-field label="Reseller Name" v-model="reseller[0].name"  readonly></v-text-field>
                      </v-col>
                      <v-col cols="12" md="12" v-else>
                        <v-text-field label="Business Name" v-model="bprofiles.name" readonly></v-text-field>
                      </v-col>
                      <v-col cols="12" md="12" v-if="reseller.length > 0">
                        <v-text-field label="Reseller Email" v-model="reseller[0].email"  readonly></v-text-field>
                      </v-col>
                      <v-col cols="12" md="12" v-else>
                        <v-text-field label="Business Email" v-model="bprofiles.email" readonly></v-text-field>
                      </v-col>
                      <v-col cols="12" md="12" v-if="reseller.length > 0">
                        <v-text-field label="Reseller Phone Number" v-model="reseller[0].mobile_phone" readonly></v-text-field>
                      </v-col>
                      <v-col cols="12" md="12" v-else>
                        <v-text-field label="Business Phone Number" v-model="bprofiles.mobile_phone" readonly></v-text-field>
                      </v-col>
                      <v-col cols="12" md="12" v-if="reseller.length > 0">
                        <v-text-field label="Reseller Address" v-model="reseller[0].address"  readonly></v-text-field>
                      </v-col>
                      <v-col cols="12" md="12" v-else>
                        <v-text-field label="Business Address" v-model="bprofiles.address" readonly></v-text-field>
                      </v-col>
                      <v-col cols="12" md="12" v-if="reseller.length > 0">
                        <v-text-field label="Reseller ID" v-model="reseller[0].id"   readonly></v-text-field>
                      </v-col>
                      <v-col cols="12" md="12" v-else></v-col>
                  </v-row>
                  <div v-if="reseller.length > 0" class="text-end">
                    <v-btn @click="openModel" class="my-2 cred">CHANGE RESELLER</v-btn>
                  </div>
                 <div v-else></div>
                </v-form>
              </v-card>
            </v-tab-item>
          </v-tabs-items>
    <v-dialog v-model="showModel" persistent width="800">
      <v-card>
        <v-card-title class="text-h5 grey lighten-2">
          Change Reseller
        </v-card-title>
        <v-card-text>
          <v-form ref="form" v-model="valid">
              <v-row >
                <v-col cols="12" md="12" class="py-0" >
                  <v-textarea :rules="reasonRules" v-model="reason_text" rows="3" row-height="20" label="Reason For Change"></v-textarea>
                </v-col>
                <v-col cols="12" md="6" class="py-0">
                  <v-text-field dense :rules="new_reseller_idRules" v-model="new_reseller_id" label="Switch to Reseller ID"></v-text-field>
                </v-col>
                <v-col cols="12" md="6" class="py-0" v-if="reseller.length > 0 ">
                  <v-text-field dense v-model="reseller[0].id" readonly label="Old Reseller ID"></v-text-field>
                </v-col>
                <v-col cols="12" md="6" class="py-0" v-else></v-col>
                <v-col cols="12" md="12" class="py-0" v-if="reseller.length > 0 ">
                  <v-text-field  v-model="reseller[0].email" readonly label="Old Reseller Email"></v-text-field>
                </v-col>
                <v-col cols="12" md="12" class="py-0" v-else></v-col>
              </v-row>
          </v-form>
        </v-card-text>
        <div class="text-end">
            <v-btn class="my-2 me-3 cred" v-if="userdata.id != null" :disabled="!valid" @click="changeResellerbyreseller">UPDATE</v-btn>
            <v-btn class="my-2 me-3 cred" v-else :disabled="!valid" @click="changeReseller">UPDATE</v-btn>
            <v-btn @click="showModel = false" class="my-2 me-3 cred">CLOSE</v-btn>
        </div>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import axios from "axios";
export default {
  name: 'BussinessProfile',
  components: {},
  data() {
    return {
      bprofiles: [],
      formData: [],
      userdata: [],
      reseller: [],
      subids: [],
      tabsarray: [],
      new_reseller_id: '',
      reason_text: '',
      imagepath: '',
      files: [],
      readers: [],
      isHidden: false,
      isimageerror: false,
      password  : '',
      valid: false,
      categories: '',
      subcategories: '',
      allcities: '',
      allstates: '',
      stateId: '',
      catId: '',
      activeFilePickerId: null,
      showDeleteAvatar: false,
      tab: null,
      showModel: false,
      snackbar: false,
      text: '',
      timeout: -1,
      res: 'RES',
      url:'https://bizlx.itechvision.in',
      // url:'http://127.0.0.1:8000',
      nameRules: [
        v => !!v || 'Store/Bussine Name is required.',
        v => v.length <= 100 || 'Name must be less than 100 characters.',
      ],
      reasonRules: [
        v => !!v || 'Reason is required.',
        v => v.length <= 100 || 'Reason must be less than 100 characters.',
      ],
      new_reseller_idRules: [
        v => !!v || 'Reseller Id is required.',
      ],
      addressRules: [
        v => !!v || 'Address is required.',
      ],
      catRules: [
        v => !!v || 'Category is required.',
      ],
      subcatRules: [
        v => !!v || 'Sub category is required.',
      ],
      stateRules: [
        v => !!v || 'State is required.',
      ],
      cityRules: [
        v => !!v || 'City is required.',
      ],
      usernameRules: [
        v => !!v || 'Username is required.',
        v => v.length <= 80 || 'Username must be less than 80 characters.',
        v => (v.split(' ').length <= 1) || 'space not allowed',
        v => /^[\w\s]+$/.test(v) || 'Username may only contain letters, numbers, dashes and underscores'
      ],
      mobileRules: [
        v => !!v || 'Mobile number is required.',
        v => v.length != 9 ||  'Mobile number must be 10 characters.',
        //v => v.length > 10 || 'Mobile number must be 103 characters.',
      ],

      emailRules: [
        v => !!v || 'E-mail is required.',
        v => /.+@.+/.test(v) || 'E-mail must be valid.',
      ],
      passwordRules: [
        v => !!v || 'Password is required.',
        v => /[A-Z]/.test(v) || 'Use upper case.',
        v => /[a-z]/.test(v) || 'Use lower case.',
        v => /[0-9]/.test(v) || 'Use number.',
        v => /[#?!@$%^&*-]/.test(v) || 'Use special character.',
        v => v.length >= 8 || 'Password must be greater than 8 characters.',
        v => v.length <= 15 || 'Password must be less than 15 characters.',
      ],
    }
  },
  computed: {
    lastWord() {
      const words = this.bprofiles.name.split(' ');
      return words[words.length - 1];
    },
    firstCharOfLastWord() {
      return this.lastWord.charAt(0);
    }
  },
  mounted() {
    this.getCategory();
    this.getsubCategory();
    this.getCities();
    this.getStates();
    this.addFiles();
    this.user_id();
    this.userdata = this.user_id();
    if(this.userdata.id != null){
      this.getBusinessprofilebyreseller();
    }else{
      this.getBusinessprofile();
    }
  },
  methods: {
    user_id() {
      return this.$store.state.userId;
    },
    addFiles(){

      this.files.forEach((file, f) => {
        this.readers[f] = new FileReader();
        this.readers[f].onloadend = () => {
          let fileData = this.readers[f].result
          let imgRef = this.$refs.file[f]
          imgRef.src = fileData
          // send to server here...
        }
        this.readers[f].readAsDataURL(this.files[f]);
      })
    },
    openModel() {
      this.showModel = true;
    },
    getBusinessprofile() {
      axios.get('business/profile')
          .then((resp) =>{
            this.bprofiles = resp.data.buser;
            this.stateId = resp.data.buser.state_id;
            this.catId = resp.data.buser.cat_id;
            this.imagepath = resp.data.buser.user_avatar;
            this.reseller = resp.data.reseller_data;
            this.tabsarray = resp.data.tabs;
            this.getCities();
            this.getsubCategory();
            for (let i = 0; i < resp.data.subcatids.length; i++) {

              this.subids.push(
                   resp.data.subcatids[i].id
            );
            }
          })
    },
    getBusinessprofilebyreseller() {

      axios.post('reseller/business/profile',{userId:this.userdata.id})
          .then((resp) =>{
            this.bprofiles = resp.data.buser;
            this.stateId = resp.data.buser.state_id;
            this.catId = resp.data.buser.cat_id;
            this.imagepath = resp.data.buser.user_avatar;
            this.reseller = resp.data.reseller_data;
            this.getCities();
            this.getsubCategory();
            for (let i = 0; i < resp.data.subcatids.length; i++) {

              this.subids.push(
                   resp.data.subcatids[i].id
            );
            }
          })
    },
    changeReseller(){
      const config = {
        headers: { 'content-type': 'multipart/form-data' }
      }
      let data = {
        old_reseller_id: this.reseller[0].id,
        new_reseller_id: this.new_reseller_id,
        reason_text: this.reason_text,
      };
      axios.post('update/reseller/id',data,config)
          .then((resp) =>{
            if( resp.data.success == true ){
              this.snackbar = true;
              this.text = resp.data.message;
              this.showModel = false;
              this.timeout = 3000;
              this.getBusinessprofile();
            }else{
              this.snackbar = true;
              this.text = resp.data.message;
              this.showModel = false;
              this.timeout = 3000;
              this.getBusinessprofile();
            }

          });
      this.$refs.form.validate();
      this.$refs.form.reset();
    },
    changeResellerbyreseller(){
      const config = {
        headers: { 'content-type': 'multipart/form-data' }
      }
      let data = {
        userId:this.userdata.id,
        old_reseller_id: this.reseller[0].id,
        new_reseller_id: this.new_reseller_id,
        reason_text: this.reason_text,
      };
      axios.post('reseller/update/reseller/id',data,config)
          .then((resp) =>{
            if( resp.data.success == true ){
              this.snackbar = true;
              this.text = resp.data.message;
              this.showModel = false;
              this.timeout = 3000;
              this.getBusinessprofilebyreseller();
            }else{
              this.snackbar = true;
              this.text = resp.data.message;
              this.showModel = false;
              this.timeout = 3000;
              this.getBusinessprofilebyreseller();
            }

          });
      this.$refs.form.validate();
      this.$refs.form.reset();
    },
    updateProfile1() {
      const config = {
        headers: { 'content-type': 'multipart/form-data' }
      }
      let data = {
        name: this.bprofiles.name,
        email: this.bprofiles.email,
        username: this.bprofiles.username,
        mobile_phone: this.bprofiles.mobile_phone,
        user_avatar: this.files[0],
      };
      axios.post('update/business/profile',data,config)
          .then((resp) =>{
            this.snackbar = true;
            this.text = resp.data.buser;
            this.timeout = 3000;
          })
          .catch(error => {
            // Handle the error
            var data = error.toJSON();
            if(data.status == 400){
              this.snackbar = true;
              this.text = "something went wrong.";
              this.timeout = 3000;
            }
          });
      this.$refs.form.validate();
    },
    updateProfileByreseller() {
      const config = {
        headers: { 'content-type': 'multipart/form-data' }
      }
      let data = {
        name: this.bprofiles.name,
        userId:this.userdata.id,
        email: this.bprofiles.email,
        username: this.bprofiles.username,
        mobile_phone: this.bprofiles.mobile_phone,
        user_avatar: this.files[0],
      };
      axios.post('reseller/update/business/profile',data,config)
          .then((resp) =>{
            this.snackbar = true;
            this.text = resp.data.buser;
            this.timeout = 3000;
          })
          .catch(error => {
            // Handle the error
            var data = error.toJSON();
            if(data.status == 400){
              this.snackbar = true;
              this.text = "something went wrong.";
              this.timeout = 3000;
            }
          });
      this.$refs.form.validate();
    },
    updateProfile2() {
      const config = {
        headers: { 'content-type': 'multipart/form-data' }
      }
      let data = {
        cat_id: this.catId,
        subcat_id: this.subids,
        state_id: this.bprofiles.state_id,
        city_id: this.bprofiles.city_id,
        gst: this.bprofiles.gst,
        address: this.bprofiles.address,
        about: this.bprofiles.about,
        mobile_phone: this.bprofiles.mobile_phone,
        service_text: this.bprofiles.service_text,
        
      };

      const posData = {
        tabids : this.tabsarray.map( tab => tab.id),
        items : this.tabsarray.map( tab => tab.tab_content)
      };
      
      axios.post('update/editor/array',posData)
            .then((resp) => {
              if( resp.data.success == true ){
                console.log('Editors data updated successfully!');
              }else{
                console.log('Something went wrong!');
              }
            });

      axios.post('update/business/profile2',data, config)
            .then((resp) => {
              this.snackbar = true;
              this.text = resp.data.buser;
              this.timeout = 3000;
            })
            .catch(error => {
              // Handle the error
              var data = error.toJSON();
              if (data.status == 400) {
                this.snackbar = true;
                this.text = "something went wrong.";
                this.timeout = 3000;
              }
            });
    },
    updateProfile2byreseller() {
      const config = {
        headers: { 'content-type': 'multipart/form-data' }
      }
      let data = {
        userId:this.userdata.id,
        cat_id: this.catId,
        subcat_id: this.subids,
        state_id: this.bprofiles.state_id,
        city_id: this.bprofiles.city_id,
        gst: this.bprofiles.gst,
        address: this.bprofiles.address,
        about: this.bprofiles.about,
        mobile_phone: this.bprofiles.mobile_phone,
        service_text: this.bprofiles.service_text,
      };
      axios.post('reseller/update/business/profile2',data, config)
            .then((resp) => {
              this.snackbar = true;
              this.text = resp.data.buser;
              this.timeout = 3000;
            })
            .catch(error => {
              // Handle the error
              var data = error.toJSON();
              if (data.status == 400) {
                this.snackbar = true;
                this.text = "something went wrong.";
                this.timeout = 3000;
              }
            });
    },
    updatePassword() {
      axios.post('update/business/password',{
        password: this.password,
      },)
          .then((resp) =>{
            this.snackbar = true;
            this.text = resp.data.buser;
            this.timeout = 3000;
          })
          .catch(error => {
            // Handle the error
            var data = error.toJSON();
            if(data.status == 400){
              this.snackbar = true;
              this.text = "something went wrong.";
              this.timeout = 3000;
            }
          });
      this.$refs.form.validate();
      this.$refs.form.reset();
    },
    updatePasswordbyreseller() {
      axios.post('reseller/update/business/password',{
        userId:this.userdata.id,
        password: this.password,
      },)
          .then((resp) =>{
            this.snackbar = true;
            this.text = resp.data.buser;
            this.timeout = 3000;
          })
          .catch(error => {
            // Handle the error
            var data = error.toJSON();
            if(data.status == 400){
              this.snackbar = true;
              this.text = "something went wrong.";
              this.timeout = 3000;
            }
          });
      this.$refs.form.validate();
      this.$refs.form.reset();
    },
    getCategory() {
      this.subids = [];
      axios.get('category')
          .then((resp) =>{
            this.categories = resp.data.categories;
          })
    },
    getsubCategory() {
      if (this.catId) {
        axios.get(`subcategories/${this.catId}`)
            .then((resp) => {
              this.subcategories = resp.data;
            })
            .catch(error => {
              console.log(error);
            });
      } else {
        this.subcategories = [];
      }
    },
    getStates() {
      axios.get('allstates')
          .then((resp) =>{
            this.allstates = resp.data.locations;
          })
    },
    getCities() {
      if (this.stateId) {
        axios.get(`states/${this.stateId}`)
            .then(response => {
              this.allcities = response.data;
            })
            .catch(error => {
              console.log(error);
            });
      }
        else {
        this.allcities = [];
      }
    },
  }
}
</script>
